import React from "react"
import { Link } from "gatsby"
import navStyles from "./nav.module.scss"
import { graphql, useStaticQuery } from "gatsby"
import logo from "./logo.gif"

const Nav = () => {
  // function pathIncludes() {
  //   if (
  //     window.location.href.includes("motion") ||
  //     window.location.href.includes("design") ||
  //     window.location.href.includes("work") ||
  //     window.location.href.includes("illustration")
  //   ) {
  //     return navStyles.active
  //   }
  // }

  const myConfig = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <nav className={navStyles.nav}>
      <h1 className={navStyles.noShow}>{myConfig.site.siteMetadata.title}</h1>
      <Link to="../">
        <img src={logo} alt="Steph Davidson" />
      </Link>

      <div className={navStyles.filter}>
        <ul>
          <li>
            <Link activeClassName={navStyles.active} to="/design">
              Design + development
            </Link>
          </li>
          <li>
            <Link activeClassName={navStyles.active} to="/motion">
              Motion
            </Link>
          </li>
          <li>
            <Link activeClassName={navStyles.active} to="/illustration">
              Illustration
            </Link>
          </li>
          <li>
            <a
              href="https://bloombergcyber.tumblr.com"
              target="_blank"
              rel="noopener noreferrer"
            >
              Art direction{" "}
              <img
                src="data:image/png;base64,
            "
                alt="new window"
              />
            </a>
          </li>
        </ul>
        <ul>
          <li>
            <a
              href="http://steph.supply"
              target="_blank"
              rel="noopener noreferrer"
            >
              Store{" "}
              <img
                src="data:image/png;base64,
            "
                alt="new window"
              />
            </a>
          </li>
          <li>
            <Link activeClassName={navStyles.active} to="/contact">
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  )
}

export default Nav
